import React, { useEffect, useState } from "react";

import { IJob } from "@core/api-types/entities/job";
import { Mapping } from "../../src/components/Mapping";
import { PageMeta } from "../../src/components/Mapping/types";

type Props = Readonly<{
  job: IJob;
  token: string;
}>;

const META_ROLE = "zoning-pagesmeta-json";
const MAPPING_REVIEWED_XML = "mapping-reviewed-xml";
const MAPPING_TAXONOMY_JSON = "mapping-taxonomy-json";

export const MappingReview = ({ job, token }: Props) => {
  const [taxonomy, setTaxonomy] = useState(null);
  const [innodom, setInnodom] = useState<string | null>(null);
  const [pageMeta, setPageMeta] = useState<Array<PageMeta> | null>(null);
  const [error, setError] = useState<Error | null>(null);
  const userKey = atob(token).split(":")[0];
  useEffect(() => {
    const innodomContent = job.contents.find(c => c.role === MAPPING_REVIEWED_XML);
    if (!innodomContent) {
      setError(new Error("Unable to find reviewed Innodom."));
      return;
    }
    fetch(innodomContent.uri, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(res => res.text())
      .then(res => {
        setInnodom(res);
      })
      .catch(setError);
  }, [job.id, token]);
  useEffect(() => {
    const taxonomyContent = job.contents.find(c => c.role === MAPPING_TAXONOMY_JSON);
    if (!taxonomyContent) {
      setError(new Error("Unable to find mapping taxonomy."));
      return;
    }
    fetch(taxonomyContent.uri, {
      headers: {
        Authorization: `Basic ${token}`,
      },
      method: "GET",
    })
      .then(res => res.json())
      .then(res => {
        setTaxonomy(res);
      })
      .catch(setError);
  }, [job.id, token]);
  useEffect(() => {
    if (pageMeta === null) {
      const zoningMeta = job.contents.find(c => c.role === META_ROLE);
      if (!zoningMeta) {
        setError(new Error("Unable to fetch page metadata."));
        return;
      }
      fetch(zoningMeta.uri, {
        headers: {
          Authorization: `Basic ${token}`,
        },
        method: "GET",
      })
        .then(res => res.json())
        .then(result => {
          if (
            result !== null &&
            typeof result === "object" &&
            !Array.isArray(result) &&
            Array.isArray(result.pages)
          ) {
            setPageMeta(
              result.pages.map((p: any) => ({
                height: parseInt(p.images.height, 10),
                width: parseInt(p.images.width, 10),
              })),
            );
            return;
          }
          setError(new Error("Unable to fetch page metadata."));
          setPageMeta(null);
        });
    }
  }, [job.id, token]);
  if (error) {
    return <div>Error: {error.message}</div>;
  }
  if (!taxonomy || !innodom || !pageMeta) {
    return <div>Loading...</div>;
  }
  const imageUris = job.contents
    .filter(c => c.role === "zoning-pagehr-png")
    .map(c => `${c.uri}?token=${userKey}`);
  return (
    <Mapping
      aborting={false}
      imageUris={imageUris}
      jobId={job.id}
      onSave={async () => {
        console.warn("This should never be called.");
      }}
      pageMeta={pageMeta}
      rawTaxonomy={taxonomy}
      review={true}
      saving={false}
      xml={innodom}
    />
  );
};
MappingReview.displayName = "MappingReview";
