import cx from "classnames";
import React, { useRef } from "react";

import { IState, Page as PageType } from "../../types";
import { useScrollToElement } from "../useScrollToElement";
import { Page } from "./Page";

type Props = Readonly<{
  activeDataPoint: IState["activeDataPoint"];
  onSelectBlock: (id: string, text: string) => void;
  pages: Array<PageType>;
  selectedBlock: string | null;
  viewWidth: number;
}>;

export const Pages = ({ activeDataPoint, onSelectBlock, pages, selectedBlock }: Props) => {
  const scrollRef = useRef<HTMLDivElement>(null);
  useScrollToElement(
    activeDataPoint,
    selectedBlock === null ? null : `zone-${selectedBlock}`,
    scrollRef,
  );
  return (
    <div className={cx("pages", { selecting: activeDataPoint.groupKey !== "" })} ref={scrollRef}>
      {pages.map(p => (
        <Page activeBlockId={selectedBlock} key={p.id} onSelectBlock={onSelectBlock} page={p} />
      ))}
    </div>
  );
};

Pages.displayName = "Pages";
