import cx from "classnames";
import React, { useMemo, useRef } from "react";

import { InnodomContent } from "@ui/components/InnodomContent";
import { ATTR_ID, TAG_TABLE } from "@ui/components/InnodomContent/constants";
import { extractFontInformation } from "@ui/components/InnodomContent/util";
import { IState } from "../../types";
import { useScrollToElement } from "../useScrollToElement";

type Props = Readonly<{
  activeBlock: string | null;
  activeDataPoint: IState["activeDataPoint"];
  content: Element;
  imageUris: Array<string>;
  onSelectBlock: (id: string, text: string) => void;
}>;

export const Blocks = ({
  activeBlock,
  activeDataPoint,
  content,
  imageUris,
  onSelectBlock,
}: Props) => {
  const scrollRef = useRef(null);
  const selector = activeBlock === null ? null : `support-${activeBlock}`;
  useScrollToElement(activeDataPoint, selector, scrollRef);
  const wrapperClassName = useMemo(
    () =>
      cx("blocks", "h-100", "overflow-auto", "p-3", {
        selecting: activeDataPoint.groupKey !== "",
      }),
    [activeDataPoint.groupKey],
  );
  const contentBlocks: Array<Element> = useMemo(() => {
    const toReturn = [];
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < content.childNodes.length; i++) {
      const node = content.childNodes[i];
      if (node instanceof Element) {
        toReturn.push(node);
      }
    }
    return toReturn;
  }, [content]);
  const fontInformation = useMemo(() => extractFontInformation(content), [content]);
  return (
    <div className={wrapperClassName} ref={scrollRef}>
      {contentBlocks.map((block, i) => {
        const blockId = block.getAttribute(ATTR_ID);
        if (!blockId) {
          return null;
        }
        const selected = !!activeBlock && activeBlock === blockId;
        let selectedCell = null;
        if (block.nodeName === TAG_TABLE) {
          selectedCell = activeBlock;
        }
        return (
          <InnodomContent
            el={block}
            fontInformation={fontInformation}
            key={`block-${i}`}
            imageUris={imageUris}
            onSelectBlock={onSelectBlock}
            selected={selected}
            selectedCell={selectedCell}
          />
        );
      })}
    </div>
  );
};
Blocks.displayName = "Blocks";
