import cx from "classnames";
import React, { memo, useRef, useState } from "react";

import {
  ATTR_ID,
  ATTR_MAX_X,
  ATTR_MAX_Y,
  ATTR_MIN_X,
  ATTR_MIN_Y,
  TAG_COL,
  TAG_TABLE,
} from "@ui/components/InnodomContent/constants";
import { Page as PageType } from "../../types";

type Props = Readonly<{
  activeBlockId: string | null;
  onSelectBlock: (id: string, text: string) => void;
  page: PageType;
}>;

const NULL_CELL = {
  bottom: 0,
  left: 0,
  right: 0,
  top: 0,
};

export const Page = memo(({ activeBlockId, onSelectBlock, page }: Props) => {
  const pageRef = useRef<HTMLDivElement>(null);
  const [imageLoaded, setImageLoaded] = useState(false);
  let contents = null;
  let height = 0;
  if (pageRef.current && imageLoaded) {
    const scale = pageRef.current.clientWidth / page.width;
    height = page.height * scale;
    contents = page.zones.map(z => {
      const parent: Element = z.element.parentNode as Element;
      if (!parent) {
        return null;
      }
      const id = parent.getAttribute(ATTR_ID) || "";
      const zoneId = `zone-${id}`;
      const style = {
        height: z.height * scale,
        left: z.x * scale,
        top: z.y * scale,
        width: z.width * scale,
      };
      const selected = !!activeBlockId && id === activeBlockId;
      const isTable = parent.nodeName === TAG_TABLE;
      if (isTable) {
        const cells = Array.from(z.element.getElementsByTagName(TAG_COL));
        return (
          <div
            className={cx("zone", { selected })}
            key={zoneId}
            id={zoneId}
            onClick={e => {
              e.preventDefault();
              onSelectBlock(id, parent.textContent || "");
            }}
            style={style}
          >
            {cells.map(cell => {
              const cellId = cell.getAttribute(ATTR_ID) || "";
              const cellZoneId = `zone-${cellId}`;
              const cellSelected = !!activeBlockId && cellId === activeBlockId;
              const cellStyle = {
                height:
                  (parseFloat(cell.getAttribute(ATTR_MAX_Y) || "") -
                    parseFloat(cell.getAttribute(ATTR_MIN_Y) || "")) *
                  scale,
                left: parseFloat(cell.getAttribute(ATTR_MIN_X) || "") * scale,
                top: parseFloat(cell.getAttribute(ATTR_MIN_Y) || "") * scale,
                width:
                  (parseFloat(cell.getAttribute(ATTR_MAX_X) || "") -
                    parseFloat(cell.getAttribute(ATTR_MIN_X) || "")) *
                  scale,
              };
              if (
                isNaN(cellStyle.height) ||
                isNaN(cellStyle.width) ||
                isNaN(cellStyle.left) ||
                isNaN(cellStyle.top)
              ) {
                return (
                  <div
                    className={cx("zone", "zone-cell", { selected: cellSelected })}
                    key={cellZoneId}
                    id={cellZoneId}
                    onClick={e => {
                      e.preventDefault();
                    }}
                    style={NULL_CELL}
                  />
                );
              }
              return (
                <div
                  className={cx("zone", "zone-cell", { selected: cellSelected })}
                  key={cellZoneId}
                  id={cellZoneId}
                  onClick={e => {
                    e.preventDefault();
                    onSelectBlock(cellId, cell.textContent || "");
                  }}
                  style={cellStyle}
                />
              );
            })}
          </div>
        );
      }

      return (
        <div
          className={cx("zone", { selected })}
          key={zoneId}
          id={zoneId}
          onClick={e => {
            e.preventDefault();
            onSelectBlock(id, parent.textContent || "");
          }}
          style={style}
        />
      );
    });
  }
  return (
    <div className="page-wrapper">
      <div className="page" style={{ height: `${height}px` }} ref={pageRef}>
        <img
          className="position-absolute"
          onLoad={() => {
            setImageLoaded(true);
          }}
          src={page.backgroundSrc}
        />
        {contents}
      </div>
    </div>
  );
});
Page.displayName = "Page";
