import cx from "classnames";
import moment from "moment";
import React, { useEffect, useMemo, useReducer, useState } from "react";
import { Col, Row } from "reactstrap";

import "react-widgets/dist/css/react-widgets.css";

import "./styles.scss";

import { getPersistentState, setPersistentState } from "@ui/util/persistState";
import { ActionToolbar } from "./ActionToolbar";
import { TAG_CONTENT } from "./constants";
import { Document } from "./Document";
import { Questions } from "./Questions";
import { reducer, reviewCallbacks, useStateCallbacks } from "./state";
import { areAnswersValid } from "./state/areAnswersValid";
import { getDefaultState } from "./state/getDefaultState";
import { insertDataPoints } from "./state/insertDataPoints";
import { validateTaxonomy } from "./taxonomy";
import { IState, PageMeta } from "./types";

type Props = Readonly<{
  aborting?: boolean;
  imageUris: Array<string>;
  jobId?: string;
  onSave: (xml: string) => Promise<void>;
  pageMeta: Array<PageMeta>;
  rawTaxonomy: any;
  review: boolean;
  saving?: boolean;
  xml: string;
}>;

moment.locale("en");
// tslint:disable-next-line: no-var-requires
const momentLocalizer = require("react-widgets-moment");
momentLocalizer();

const LOAD_PERSISTED_STATE_MESSAGE = "Would you like to load your previous work for this job?";
const STATE_APP_ID = "mapping-workbench";
const STATE_VERSION = "1";

export const Mapping = ({
  aborting,
  imageUris,
  jobId,
  onSave,
  pageMeta,
  rawTaxonomy,
  review,
  saving,
  xml,
}: Props) => {
  const taxonomy = useMemo(() => validateTaxonomy(rawTaxonomy), [rawTaxonomy]);
  const defaultState = useMemo(() => getDefaultState(taxonomy, xml), [taxonomy, xml]);
  const [state, dispatch] = useReducer(reducer, defaultState);
  const stateCallbacks = review ? reviewCallbacks(dispatch) : useStateCallbacks(dispatch);
  const answersValid = review ? false : areAnswersValid(state.dataPoints, taxonomy);
  const [isFirstRun, setFirstRun] = useState(true);
  const contentTags = useMemo(() => state.xmlRoot.getElementsByTagName(TAG_CONTENT), [
    state.xmlRoot,
  ]);
  if (contentTags.length < 1) {
    throw new Error("Missing content from Innodom.");
  }
  const content = contentTags[0];
  useEffect(() => {
    if (!isFirstRun || !jobId) {
      return;
    }
    setFirstRun(false);
    const [, persistedState] = getPersistentState(STATE_APP_ID, jobId);
    if (persistedState !== null) {
      if (JSON.stringify(persistedState) === JSON.stringify(state.dataPoints)) {
        return;
      }
      if (window.confirm(LOAD_PERSISTED_STATE_MESSAGE)) {
        dispatch({
          persistedState: persistedState as IState["dataPoints"],
          type: "LOAD PERSISTED STATE",
        });
      }
    }
  });
  useEffect(() => {
    if (isFirstRun || !jobId) {
      return;
    }
    setPersistentState(STATE_APP_ID, jobId, STATE_VERSION, state.dataPoints);
  }, [state.dataPoints]);
  return (
    <Row className={cx("h-100", { review })}>
      <Col className="h-100 pr-0" xs={7}>
        <Document
          content={content}
          images={imageUris}
          pageMeta={pageMeta}
          review={review}
          state={state}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      </Col>
      <Col className="border-left datapoints-wrapper d-flex flex-column h-100 pl-0" xs={5}>
        {!review && (
          <ActionToolbar
            aborting={!!aborting}
            canSave={answersValid}
            onSave={async () => {
              insertDataPoints(state.xmlRoot, state.dataPoints, taxonomy);
              const serializer = new XMLSerializer();
              onSave(serializer.serializeToString(state.xmlRoot));
            }}
            saving={!!saving}
          />
        )}
        <Questions
          review={review}
          state={state}
          stateCallbacks={stateCallbacks}
          taxonomy={taxonomy}
        />
      </Col>
    </Row>
  );
};
Mapping.defaultProps = {
  aborting: false,
  saving: false,
};
Mapping.displayName = "Mapping";
